body{
    background-color: rgb(49, 49, 49);
    text-align: center;
    color: white;
    font-family: 'Montserrat', sans-serif;
}

#container{
    width: 60%;
    min-height: 100vh;
    margin: 0 auto;
    padding: 50px;
    position: relative;
}

@media only screen and (max-width:600px){
   #container{
       width: 100%;
   }
   #result{
       width: 350px;
   }
}


#result{
    margin-top: 30px;
    border-radius: 10px;
    border: 1px solid white;
    background-color: rgb(28, 38, 53);
    margin-bottom: 30px;
}

.column{
    padding:10px;
}